.cursorPointerOnHover:hover{
    cursor: pointer;
}
.rigtherNameModal{
    margin-left: 8%;
}
.canBeRemovedByUser td{
    /*margin-left: 8%;*/
}
.loaderBoxFc {
    position: absolute;
    left: 50px;
    top:3px;
}

.boxFC {
    max-width: 1100px;
    max-height: 1100px;
    margin: 40px auto;
}
.search-results {
    background-color: #00C0FF;
    width:40%;
    height:50%;
    position:absolute;
    z-index:10;

    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.btn-close {
    background-color: white;
float: right;
    margin-top: -150px;
}

#tableAtModal_GraficPage {
    border-radius: 2%;
    background-color: #E1E3E0;
/*text-align: center;*/
}
#tableAtModal_GraficPage thead tr{
    text-align: center;
}

#tableAtModal_GraficPage input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
#tableAtModal_GraficPage input {
    width: 100%;
}
#tableAtModal_GraficPage th {
    font-size: 20px;
    font-weight: 30;
    padding: 12px 10px;
    border-bottom: 2px solid #F56433;
    color: #F56433;
}
#tableAtModal_GraficPage td {
    padding: 10px;
    color: #8D8173;
}

#firstColumnAtModal {
    width: 40%;
}
#secondColumnAtModal {
    width: 22%;
}

.modalGrafik {
border-radius: 10%;
    height: 55%;
    width: 60%;

    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    z-index: 99999;
    /*background-color: #00C0FF;*/

}
.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.75);
    z-index: 1;
}

.mainModalDiv {

    text-align: center;
}
.mainModalDiv.h2{
    font-size: 55px;
}

#closeModalButton {
    text-align: center;
background-color: #3a3a3b;
    float: right;
    box-sizing: content-box;
    width: 1em;
    height: 1em;
    padding: 0.25em 0.25em;
    border: 0;
    border-radius: 0.25rem;
    opacity: .5;
}

.buttonRemovedWorkerInsideModal {
    margin-left: 20%;
    width: 25px;
    height: 25px;

    border-radius: 7px;
}
.buttonRemovedWorkerInsideModal:hover {
    box-shadow: 0 0 10px rgba(33,33,33,.2);
    cursor: pointer;
}
.buttonAddWorkerInsideModal {
    margin-left: 5px;
}
.selectWorkers {
    display: inline-flex;
    margin-left: 8%;
    margin-top: 2%;
}
.btnApproveWorkersAtModal {
    /*text-align: center;*/
    margin-top: 2%;
}
