*{
  padding: 0;
  margin: 0;
}
._container{
  width: 1600px;
  margin: 0 auto;
}

/* Roboto */

@font-face {
	font-family: 'Roboto Regular';
	src: url('../fonts/roboto_regular.eot');
	src: local('☺'), url('../fonts/roboto_regular.woff') format('woff'), url('../fonts/roboto_regular.ttf') format('truetype'), url('../fonts/roboto_regular.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Roboto Medium';
	src: url('../fonts/roboto_medium.eot'); /* IE 9 Compatibility Mode */
	src: url('../fonts/roboto_medium.eot?#iefix') format('embedded-opentype'), /* IE < 9 */
		url('../fonts/roboto_medium.woff2') format('woff2'), /* Super Modern Browsers */
		url('../fonts/roboto_medium.woff') format('woff'), /* Firefox >= 3.6, any other modern browser */
		url('../fonts/roboto_medium.ttf') format('truetype'), /* Safari, Android, iOS */
		url('../fonts/roboto_medium.svg#roboto_medium') format('svg'); /* Chrome < 4, Legacy iOS */
}

