.linkUnderNavbar {
    margin-top: 40px;
    background-color: #00C0FF;
}
.rainbow{
    background-color: #3a3a3b;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    padding: 8px 16px;
}
a {
    text-decoration: none;
}
.rainbow-1:hover{
    background-image: linear-gradient(90deg, #00C0FF 0%, #FFCF00 49%, #FC4F4F 80%, #00C0FF 100%);
    animation:slidebg 5s linear infinite;
    color:whitesmoke;
}
@keyframes slidebg {
    to {
        background-position:20vw;
    }
}
.tableDogovor {
    max-width: 1100px;
    max-height: 1100px;
    margin: 40px auto;
    text-align: center;
    border-collapse: collapse;
    border-spacing: 5px;
    background: #E1E3E0;
    border-radius: 20px;
}
.tableDogovor th {
    font-size: 20px;
    font-weight: 30;
    padding: 12px 10px;
    border-bottom: 2px solid #F56433;
    color: #F56433;
}

.tableDogovor td {
    padding: 10px;
    color: #8D8173;
}